<template>
    <div class="login_container" > 
        <div class="header">AliOSThings</div>
         <div style="text-align:center;font-size:35px;color:#76171A"></div>
        <!--  -->
        <div class="login_box">
            <!--  -->
            <div class= "avatar_box">
                <img src="../assets/logo.png" alt="logo">
            </div>
             <!-- v-bind@v-on :usernamepassworddefault -->
            <el-form ref="loginFromRef" :rules="rules" :model="loginForm" class="login_form" label-width="0"> 
                  <!--   prop  -->
                  <el-form-item></el-form-item>
                  <el-form-item prop="username">
                   <el-input v-model="loginForm.username" placeholder="" prefix-icon="iconfont icon-wode1"></el-input>  
                 </el-form-item>
                 
                 <!--  prop -->
                  <el-form-item  prop="password"> 
                    <el-input v-model="loginForm.password" placeholder="" prefix-icon="iconfont icon-mima" type="password"></el-input>  
                 </el-form-item>
                <!--  -->
                   <el-form-item class="btns">
                            <el-button type="success" :loading="isloading"  @click="login_wait()">
                            <span v-if="isloading">loading...</span>    
                            <span v-else> </span>
                                
                            </el-button>
                            <el-button type="info" @click="resetLoginForm()"></el-button>
                    </el-form-item> 
            </el-form>
        </div>
    </div>
</template>

<script>
export default {     // 
    data(){
        return{
                loginForm:{         //v-modelloginFrom
                    username:"",
                    password:""
                },
                rules: {
                     username: [
                      { required: true, message: '', trigger: 'blur' },
                     { min: 3, max: 10, message: '3 ~ 10  ', trigger: 'blur' }
                     ],
                     password:
                      [
                      { required: true, message: '', trigger: 'blur' },
                     { min: 6, max: 10, message: ' 6 ~ 10  ', trigger: 'blur' }
                     ]
                },
                isloading: false,
             
        };
    },
    methods:{
         //
        login_wait(){  
            this.isloading=true;
            setTimeout(() =>{  //1000ms
              this.isloading=false;  
              this.login();
            },1000);    
        },
        login(){
            //validatevalid{}
            this.$refs.loginFromRef.validate(async valid=>{
                    if(!valid) return;
                    //  console.log(data)
                    // data:resdatadatadatares
                    
                    const {data:res} = await this.$http.post("login",this.loginForm); 
                    // console.log("login.vue:"+this.loginForm);
                    if(res.flag == 'ok'){
                        this.$message.success("");
                        // JSON
                        window.sessionStorage.setItem("user",JSON.stringify(res.user));
                        //  console.log( window.sessionStorage.getItem("user"));
                        this.$router.push({path:"/home"}); //routerpath/home
                    }
                    else{
                        this.$message.error("");
                    }
                })
        },
        //
        resetLoginForm(){
            //console.log('klksdlsd')
            //$res+resetFields
            this.$refs.loginFromRef.resetFields();
        },
       
    }
}   
</script>

<style lang="less" scoped>

.login_container{
    background: url('../assets/login.jpg');
    // background-position: center;
    background-repeat:no-repeat;
    height: 100%;
    width: 100%;
     background-size:100%;
    .header{ //
        display: flex;
        margin: auto;
        justify-content: center; 
        padding-top:40px ;
        font-size:40px;
        color: #76171A;
        font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
     }
}
.login_box{
    width:300px;
    height:300px;
    background-color:rgba(255, 255, 255, 0.1);
    border-radius:3px;
    position:absolute;
    left: 80%;
    top: 50%;
    transform: translate(-50%,-50%);
    .avatar_box{
        width: 80px;
        height: 80px;
        border: 1px solid #eee;
        border-radius: 50%;  //
        box-shadow: 0 0 10px #ddd; //
        position: absolute;
        left: 50%;
        transform:translate(-50%,-50%) ; //00
        background-color: rgba(255, 255, 255, 0.2);
        img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            // background-color:;
        }
    }
}
.login_form{
     position: absolute;
     bottom: 0;
     width: 100%;
     padding: 0 5px 10px 10px; //10px
     box-sizing: border-box; //border
}
.btns{
     display: flex;             //

     justify-content: center; //
}

</style>