<template>
  <div>
    <!--   -->
    <el-breadcrumb class="home" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/user' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/user' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card style="border:none;" body-style="background:#272B34">
      <!-- gutter-- -->
      <el-row :gutter="20">
        <!-- slot--,append -->
        <el-col :span="8">
          <el-input v-model="queryInfo.query" placeholder="" clearable @clear="getUserList()">
            <el-button type="primary" slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
          </el-input>
        </el-col>
        <!--  span,24 -->
        <el-col :span="2">
          <el-button type="primary" @click="addUserButton"></el-button>
        </el-col>
      </el-row>
      <!-- {{total}}
      {{userlist}}-->
    </el-card>
    <!--  :data,userlist,propuserlist -->
    <el-table :data="userlist" 
    style="margin:auto;border-radius:5px;border:1px solid #fff;box-shadow:10px 10px 5px #000;width:98%" 
    :cell-style="bodyCell"
    :header-cell-style="headerCell"
    >
      <el-table-column type="index"></el-table-column>
      <el-table-column label="" prop="username"></el-table-column>
      <el-table-column label="" prop="contacts"></el-table-column>
      <el-table-column label="" prop="role"></el-table-column>
      <el-table-column label="" prop="state">
        <template slot-scope="scope">
          <!-- {{scope.row}}  -->
          <el-switch
            active-color="#f00"
            v-model="scope.row.state"
            :disabled="scope.row.role==''"
            @change="userStateChanged(scope.row)"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width="320px">
        <!--  -->
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" @click="updateDialogButton(scope.row.id)"></el-button>
          <el-button icon="el-icon-notebook-2" @click="detailUser(scope.row.id)"></el-button>
          <el-button type="danger" icon="el-icon-delete" @click="deleteUser(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="block" >
      <span class="demonstration"></span>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[2 , 5, 10, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <!-- Dialog -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogColsed()">
      <el-form :model="addFrom"  :rules="addFormRlues" ref="addFormRef" label-width="70px">
        <el-form-item label="" label-width="80px" prop="username">
          <el-input v-model="addFrom.username"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px" prop="password">
          <el-input v-model="addFrom.password"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px" prop="contacts">
          <el-input v-model="addFrom.contacts"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class>
        <el-button @click="addDialogVisible=false"></el-button>
        <el-button @click="addUser()"></el-button>
      </span>
    </el-dialog>  
    <!-- Dialog -->
    <el-dialog title="" :visible.sync="updateDialogVisible" width="50%" @close="updateDialogColsed()">
          <el-form :model="updateForm"  :rules="updateFormRlues" ref="updateRef" label-width="70px">
              <el-form-item label-width="80px" label="" prop="usernmae">
                  <el-input v-model="updateForm.username"> </el-input>
              </el-form-item>
              <el-form-item 
              label-width="80px"
              label="" 
              prop="password">
                <el-input :disabled="updateForm.role!=''" v-model="updateForm.password"> </el-input>
            </el-form-item>
              <el-form-item label="" label-width="80px" prop="contacts">
                  <el-input v-model="updateForm.contacts"> </el-input>
              </el-form-item>
          </el-form>
          <span slot="footer">
              <el-button @click="updateDialogVisible=false"></el-button>
              <el-button @click="updateUser()"></el-button>
          </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="detailDialogVisible" width="50%">
          <div style="font-size:15px">
             <strong style="display:inline-block;width:80px;text-align:center"></strong><span>{{this.detailFrom.username}}</span>
            <el-divider></el-divider>
             <strong style="display:inline-block;width:80px;text-align:left"></strong><span>{{this.detailFrom.contacts}}</span>
            <el-divider></el-divider>
             <strong style="display:inline-block;width:80px;text-align:center"></strong><span>{{this.detailFrom.state}}</span>
            <el-divider></el-divider>
            <strong style="display:inline-block;width:80px;text-align:center"></strong><span>{{this.detailFrom.role}}</span>
            <el-divider></el-divider>
            <strong style="display:inline-block;width:80px;text-align:center"></strong><span>{{this.detailFrom.address}}</span>
            <el-divider></el-divider>
          </div>
          <span slot="footer">
              <el-button @click="detailDialogVisible=false"></el-button>
          </span>
    </el-dialog>

  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        query: "", //
        pageNum: 1, //
        pageSize: 5, //
      },
      userlist: [],
      total: 0,
      updateDialogVisible: false,  //
      addDialogVisible: false, //
      addFrom: { //
        username: "",
        password: "",
        contacts: "",
      },
      addFormRlues: { //
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "3 ~ 10  ",
            trigger: "blur",
          },
        ],
        contacts: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 0,
            max: 20,
            message: "1 ~ 20 ",
            trigger: "blur",
          },
        ],
      },
      updateForm:{  //
        
      },
      updateFormRlues:{  //
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "3 ~ 10  ",
            trigger: "blur",
          },
        ],
         contacts: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 0,
            max: 20,
            message: "1 ~ 20  ",
            trigger: "blur",
          },
        ],
      },
      detailFrom:{

      },
      detailDialogVisible:false,
   }; 
  },
  //
  created() {
    this.getUserList();
  },
  methods: {
    headerCell({rowIndex,columnIndex,row,column}){
      // console.log(rowIndex,columnIndex,row,column)
      return "background:#272B34;color:#DEB185;text-align:center;font-size:15px"
    },
    //
    bodyCell(e){
      // console.log(e)
      if(e.columnIndex == 6)
      {
      return "background:#409EFF;color:#fff;text-align:center";
      }
      else{
        return "background:#409EFF;color:#fff;border-right:1px solid #fff;text-align:center";
      }
    },
    //
    async getUserList() {
      const { data: res } = await this.$http.get("allUser", {
        params: this.queryInfo,
      });
      this.userlist = res.userAll;
      this.total = res.numbers;
    },
    // ,
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getUserList();
    },
    //
    handleCurrentChange(newSize) {
      this.queryInfo.pageNum = newSize;
      this.getUserList();
    },
    //
    async userStateChanged(userInfo) {
      //const {data:res} = await this.$http.put('updateState?id='+userInfo.id+'&state='+userInfo.state);
      const { data: res } = await this.$http.put(
        `updateState?id=${userInfo.id}&state=${userInfo.state}`
      );
      if (res != "success") {
        userInfo.id = !userInfo;
        return this.$message.error("");
      }
      this.$message.success("");
    },
    // 
    addDialogColsed() {
      this.$refs.addFormRef.resetFields(); //
    },
    //
    async detailUser(rowId){
      // console.log("row",rowId)
      this.detailDialogVisible = true; //
      const {data:res} = await this.$http.get("getUpdate?id="+rowId);
      // console.log(res)
      this.detailFrom =res;
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return;
        const {data:res} = await this.$http.post("addUser", this.addFrom);
        //  console.log("");
        // console.log(res);
        
          if (res !="success") {  //res="success"res
            this.$message.error("");
          } else this.$message.success("");
           this.addDialogColsed();  //
           this.addDialogVisible = false; // dialog
           this.getUserList(); //
      });
    },
    //dialog
    addUserButton() {
      this.addDialogVisible = true;
    },
    // 
    async deleteUser(id){
      //  
    const confirmResult = await this.$confirm('','',{
        confirmButtonText : '', 
        cancelButtonText:'',
        type:'warning'
      }).catch(err=>err);
      if(confirmResult!='confirm'){
        return this.$message.info("");
      }
      const {data:res} =await this.$http.delete("deleteUser?id="+id);
      // console.log(res);
      if(res != "success")
      {
        return this.$message.error("");
      }
       this.$message.success("");
       this.getUserList();
    },
    // 
    updateDialogColsed(){
      this.$refs.updateRef.resetFields();//update
    },
    async updateDialogButton(id){
      this.updateDialogVisible = true; //
      const {data:res} = await this.$http.get("getUpdate?id="+id);
      this.updateForm = res;
      console.log(this.updateForm);
    },
    updateUser()
     {
       this.$refs.updateRef.validate(async (valid)=>{
          if(!valid) return;
          const {data:res} = await this.$http.post("editUser",this.updateForm);
            // console.log(res);
           if(res!="success"){
             this.$message.error("");
           }else this.$message.success("");
            this.updateDialogColsed();
            this.updateDialogVisible=false;
            this.getUserList();
       });
      
     },
 },
};
</script>
<style lang="less">
.home /deep/ .el-breadcrumb__inner.is-link{
  color:#DEB185;
}
.home /deep/ .el-breadcrumb__inner.is-link:hover{
  color: blue;
}
.el-breadcrumb {
  //
  margin-bottom: 15px;
  font-size: 15px;
}

// 
.el-pagination__total{
  color:#DEB185 !important;
}
.el-input--mini .el-input__inner
{
  color:#DEB185 !important;
  background: #272B34;
}

.el-pagination__jump{
  color:#DEB185 !important;
    .el-input__inner{
    background: #272B34;
    color:#DEB185;
  }
}
.el-pager li{
  color:#DEB185 !important;
  background: #272B34 !important;
}
.el-pager li.active{
  color: #fff !important;
  background:#409EFF !important;
}
 .el-pagination .btn-next{
  content:'';
  background: #272B34 !important;
  color: #fff !important;
   border:1px solid rgb(163, 162, 162) !important;
}
 .el-pagination .btn-prev{
  content:'';
  background: #272B34 !important;
  color: #fff !important;
   border:1px solid rgb(163, 162, 162) !important;
}
.block {
  //
  margin-top:50px;
}
</style>