<template >
  <div class="wrap">
    <!--  -->
    
      <el-breadcrumb class="home" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/equipment' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/equipment' }"></el-breadcrumb-item>
      </el-breadcrumb>
    
    <!--  -->
    <el-card  style="background:#272B34;color:#DEB185;border:none">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input v-model="queryInfo.query" placeholder="ID">
            <el-button slot="append" icon="el-icon-search" @click="findEqInfo"></el-button>
          </el-input>
        </el-col>
        <el-col :span="2">
          <el-button @click="bindEqDialogOpen()" type="success"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <el-card style="background:#272B34;color:#DEB185;border:none">
      <el-button type="warning" @click="removeBindEq"></el-button>
    </el-card>
    <!--  -->
    <div>
      <el-table
        border
        :data="EqList"
        style="width:100%;border:1px solid #fff;border-radius:5px;box-shadow:-10px 10px 5px #000;"
        @selection-change="selsChange"
        :cell-style="bodyCell" 
        :header-cell-style="headerCell"
      >
        <el-table-column type="selection" width="55"></el-table-column>

        <el-table-column prop="eqName" label="ID" width="100px"></el-table-column>
        <el-table-column prop="state" label="" width="100px" style="align:center">
          <template slot-scope="scope">
            <span v-if="scope.row.state">
              <el-button type="success"></el-button>
            </span>
            <span v-else>
              <el-button type="danger"></el-button>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="userName" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.userName==='' "></span>
            <span v-else>{{scope.row.userName}}</span>
            <!-- {{scope.row.userName}} -->
          </template>
        </el-table-column>

        <el-table-column prop="state" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.state">true</span>
            <span v-else>
              <el-button type="danger" @click="updateEqDialog(scope.row)"></el-button>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop>
           <template slot-scope="scope">
           <router-link  :to="{name:'getEqData',params:{data:scope.row}}"> 
             <el-button icon="el-icon-view">
               
             </el-button>
             </router-link>
          </template>
        </el-table-column>
        <el-table-column prop label="">
          <template slot-scope="scope">
              <el-button @click="detailEq(scope.row)" type="warning" icon="el-icon-notebook-2" size="small"></el-button>
            <el-button @click="deleteEq(scope.row)" type="danger" icon="el-icon-delete" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <div class="block">
      <span class="demonstration"></span>
      <!-- @size-change="handleSizeChange"
      @current-change="handleCurrentChange"-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[3,5,8]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <!-- bindDialog -->
    <el-dialog title="" :visible.sync="isDialogVisible" width="40%"  @close="bindEqDialogClose()"> 
      <el-form  :model="eqForm" label-width="75px" ref="bindEqFormRef">
        <el-form-item prop="eqName" label=":">
          <el-input v-model="eqForm.eqName" placeholder="ID"></el-input>
        </el-form-item>
        <el-form-item prop="userName" label=":">
          <el-input v-model="eqForm.userName" placeholder=""></el-input>
        </el-form-item>
      </el-form>

      <el-row slot="footer">
        <el-col :span="2" :push="5">
          <el-button type="success" @click="bindEq" plain></el-button>
        </el-col>
        <el-col :span="4" :push="9">
          <el-button type="info" plain @click="bindEqDialogClose()"></el-button>
        </el-col>
      </el-row>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="isDialogDetail" width="50%">
          <div style="font-size:15px">
            <el-row :gutter="20">
              <el-col :span="12"><strong class="detailSpan">ID</strong>{{this.detailData.productKey}}</el-col>
              <el-col :span="12"><strong class="detailSpan"></strong>{{this.detailData.productSecret}}</el-col>
            </el-row>
            <el-divider></el-divider>
             <el-row :gutter="20">
              <el-col :span="12"><strong class="detailSpan">ID</strong>{{this.detailData.eqName}}</el-col>
              <el-col :span="12"><strong class="detailSpan"></strong>{{this.detailData.deviceSecret}}</el-col>
            </el-row>
            <el-divider></el-divider>
             <el-row :gutter="20">
              <el-col :span="12"><strong class="detailSpan"></strong>{{this.detailData.userName}}</el-col>
              <el-col :span="12"><strong class="detailSpan"></strong>{{this.detailData.state?'':""}}</el-col>
            </el-row>
            <el-divider></el-divider>
          </div>
          <span slot="footer">
              <el-button @click="isDialogDetail=false"></el-button>
          </span>
    </el-dialog>

  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        query: "", //
        pageNum: 1, //
        pageSize: 3, //
      },
      sles: [],
      EqList: [],
      total: 0,
      // bindDialog
      eqForm: {
        eqName: "",
        userName: "",
      },
      isDialogVisible: false,
      isDialogDetail:false,
      detailData:{},
    };
  },
  created() {
    this.getAllEq();
  },
  methods: {
    //
    headerCell({rowIndex,columnIndex,row,column}){
      // console.log(rowIndex,columnIndex,row,column)
      return "background:#272B34;color:#DEB185;text-align:center;font-size:15px"
    },
    //
    bodyCell(e){
      // console.log(e)
      if(e.columnIndex == 5)
      {
      return "background:#409EFF;color:#fff;text-align:center";
      }
      else{
        return "background:#409EFF;color:#fff;border-right:1px solid #fff;text-align:center";
      }
    },
    // 
    async getAllEq() {
      const { data: res } = await this.$http.get("getEqList", {
        params: this.queryInfo,
      });
      //  console.log(res);
      this.EqList = res.allEq;
      //  console.log(this.EqList);
      this.total = res.numbers;
    },
    handleSizeChange(newsize) {
      this.queryInfo.pageSize = newsize;
      this.getAllEq();
    },
    handleCurrentChange(currentPage) {
      this.queryInfo.pageNum = currentPage;
      this.getAllEq();
    },
    bindEqDialogOpen() {
      // console.log(""),
      this.isDialogVisible = true;
    },
    bindEqDialogClose() {
      // alert("dian");
      this.$refs.bindEqFormRef.resetFields();
      this.isDialogVisible = false;
    },
    // ,ID
    findEqInfo() {
      this.getAllEq();
    },
    // Dialog
    updateEqDialog(scope) {
      this.eqForm.eqName = scope.eqName;
      this.bindEqDialogOpen();
    },
    // 
    async bindEq() {
      const { data: res } = await this.$http.get("bindEq", {
        params: this.eqForm,
      });
      if (res != "success") {
        this.$message.error("!");
        return;
      }
      this.$message.success("!");
      this.getAllEq();
      this.bindEqDialogClose();
    },
    detailEq(scope){
      console.log(scope)
      this.detailData = scope;
      this.isDialogDetail =true;
    },
    //id
    async deleteEq(scope) {
      const confirmResult = await this.$confirm(
        "",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult != "confirm") {
        return this.$message.info("");
      }
      // console.log(scope);
      const { data: res } = await this.$http.delete("deleteEq?id=" + scope.id);
      // console.log(res);
      if (res != "success") {
        return this.$message.error("");
      }
      this.$message.success("!!");
      this.getAllEq();
    },
    //,
    selsChange(val) {
      this.sels = val;
      // for(var i=0;i<this.sels.length;i++)
      //  console.log(this.sels)
    },
    //
    async removeBindEq() {
      //  console.log(this.sels)

      if (this.sels != null && this.sels.length > 0) {
        for (var i = 0; i < this.sels.length; i++) {
          const { data: res } = await this.$http.put(
            "removeBindEq?id=" + this.sels[i].id
          );

          if (res != "success") {
            return this.$message.error(",!");
          }
          //  console.log(this.sels[i].id)
          // console.log(res);
        }
        this.$message.success("");
        this.getAllEq();
      } else {
        console.log("");
        return this.$message.error("");
      }
    },
     getEqData(scope){
        console.log(scope)
        this.$router.push("/");
    },
  },
};
</script>
<style lang="less" scoped>

.home /deep/ .el-breadcrumb__inner.is-link{
  color:#DEB185
}
.home /deep/ .el-breadcrumb__inner.is-link:hover{
  color: blue;
} 
.wrap {
  background-color: #272B34;
  height: 100%;
}
el-table {
  text-align: center;
}
.detailSpan{
  display:inline-block;
  width:80px;
  text-align:center
}
</style>