<template>
  <!-- <div>:{{this.eqData}}
        <div>{{this.eqData.eqName}}</div>    
  </div>-->
  <div>
    <!--  -->
    <el-breadcrumb class='home' separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/equipment' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/equipment' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/getEqData' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="targetUser" >
      <span> 
        <strong class="Eitem">{{this.eqData.eqName}}</strong>
      </span>
      <span class="second-child" ><strong class="Eitem">{{this.eqData.userName}}</strong> </span>
      <span class="second-child" ><strong class="Eitem">{{this.current.time}}</strong> </span>
      <span class="third-child" > <el-switch v-model="refresh" active-color="#061740"></el-switch></span>
    </el-card>
    <div class="container">
        <div class="first-child">
        <el-card style="margin-bottom:20px;box-shadow:10px 10px 5px #000;text-align:center">
          <el-switch style="margin-left:20px;" :disabled="disable" v-model="current.ledSwitch" active-color="green"></el-switch>
        </el-card>
        </div>
         <div class='two-child'>
        <el-card style="margin-bottom:20px;box-shadow:10px 10px 5px #000;text-align:center">
         
          <strong class="Eitem">{{current.hum}}%</strong>
        </el-card>
        </div>
         <div class='three-child'>
        <el-card style="margin-bottom:20px;box-shadow:10px 10px 5px #000;text-align:center">
          <el-switch v-model="current.fanSwitch" :disabled="disable" style="margin-left:20px;" active-color="green"></el-switch>  
        </el-card>
        </div>
         <div class='four-child'>
        <el-card style="margin-bottom:20px;box-shadow:10px 10px 5px #000;text-align:center">
         
         <strong class="Eitem"> {{current.smoke}}ppm</strong>
        </el-card>
        </div>
    </div>
    <!--  -->
    <div>
      <el-row :gutter="20" style="margin-bottom:10px">
        <el-col :span="12">
          <el-card style="height:360px">
            <zl :params="this.params" :wd="this.current.wd" ref="child1" style="margin:auto;margin-top:10px"></zl>
            <div style="margin-top:20px;text-align:center"></div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card style="height:360px;margin:auto">
            <gz :eqName="this.eqData.eqName" :smoke="this.smokeList" ref="child2" :refresh="this.refresh"></gz>
            <!-- <gz></gz> -->
            <div style="margin-top:-25px;text-align:center"></div>
          </el-card>
        </el-col>
      </el-row>
      <!-- <el-row :gutter="20">
        <el-col :span="12">
          <el-card>
            <wd></wd>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card>
            <ph></ph>
          </el-card>
        </el-col>
      </el-row> -->
      
    </div>
  </div>
</template>
<script>
//
import wd from "./EqData/wdLegend";
import ph from "./EqData/phLegend";
import gz from "./EqData/gzLegend";
import zl from "./EqData/zlLegend";
export default {
  //
  components: {
    wd,
    ph,
    zl,
    gz,
  },
  data() {
    return {
      disable:true,
      refresh:0,
      timer:"",
      is:1,
      eqName: "sdad",
      eqData:{},
      current:{},
      smokeList:[],
      params:{    //
        productKey:'',
        productSecret:'',
        deviceSecret:'',
        eqName:'',
        userName:'',
        state:'',
      }
    };
  },
  watch:{
    refresh(e){
     if(e===true)
      {
       this.autoRefresh();
      }
      else{
        clearInterval(this.timer);
      }
    },
    smokeList(e){
      this.$refs.child2.init(this.refresh,e);
    }
  },
  created() {
    //
    clearInterval(this.timer);
    if (this.$route.params.data==null||this.$route.params.data.state != true) {
      this.$message.error( "2" );
      //
      setTimeout(() => {
        //1000ms
        this.$router.go(-1); //
      }, 2000);
    } else {
      this.eqData = this.$route.params.data;
      this.params = {...this.$route.params.data}
      // console.log("router--params",this.$route.params)
       this.getEqData();
    }
  },
  destroyed(){
    //
    clearInterval(this.timer);
  },
  methods:{
      async getEqData(){
          const { data: res } = await this.$http.get("/currentEqData",{params:this.params});
          // console.log(res);
          if(res.currentEqData&&res.smokeList){
              let {currentEqData,smokeList} =res;
              this.smokeList = smokeList;
              this.current = currentEqData;
              // this.current = res;
              // // Boolean
              this.current.ledSwitch = Boolean(this.current.ledSwitch); 
              // console.log(this.current) 
          }else{
              this.$message.error( "2" );
              //
              setTimeout(() => {
                //1000ms
                this.$router.go(-1); //
              }, 2000);
          }
      },   
      autoRefresh(){
        this.timer=setInterval(()=>{
          //
          this.getEqData();
          //ref
          this.$refs.child1.update(this.current.wd);
          // this.smokeList =[500,600,700,900]
          this.$refs.child2.update(this.refresh,this.smokeList);
          // this.current.wd++;
        },5000)
      }
  }
};
</script>
<style lang="less" scoped>
.home /deep/ .el-breadcrumb__inner.is-link{
  color:#DEB185
}
.home /deep/ .el-breadcrumb__inner.is-link:hover{
  color: blue;
} 
.targetUser{
   margin-bottom:20px;
   box-shadow:10px 10px 5px #000;
  //  color:red;
   .first-child{
     
   }
   .second-child{
     display:inline-block;
     margin-left:50px
   }
  .third-child{
     display:inline-block;
     margin-left:50px;
     position:relative;
     left:10%;
   }
}
.Eitem{
      color:#061740;
}
.container{
  display:flex;
  .first-child{
     flex:1;
     margin-right:20px;
   }
   .two-child{
     flex:1;
     margin-right:20px;
   }
   .three-child{
     flex:1;
     margin-right:20px;
   }
   .four-child{
     flex:1;
   }
 }
</style>