import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '../components/Login.vue' //Login
//
import Home from '../components/Home.vue'
//
import Welcome from '../components/Welcome.vue'
//
import User from '../components/NavList/User.vue'
//Rights
import Rights from '../components/NavList/Rights.vue'
//Sport
import Sport from '../components/NavList/Sport.vue'

import Feedback from '../components/feedback.vue'
//
import Equipment from '../components/equipment.vue'
//
import Equipment_add from '../components/equipment_add.vue'
// 
import getEqData from '../views/getEqData.vue'
Vue.use(VueRouter)

  const routes = [  //
    {
      path:"/",
      redirect:"/login",    ///loginlogin.vue
    
    },
    {
        path:"/login",
        component:Login,   ///logincomponentLogin.vue
        meta:{
          title:""
        }
    },
    {
        path:"/home", //
        component:Home, 
        redirect:"/Welcome", //welcome
        meta:{
          title:""
        },
        children:[
           {  path:"/Welcome",component:Welcome},
           {  path:"/user",component:User},
           {  path:"/rights",component:Rights},
           {  path:"/sport",component:Sport},
           {  path:"/feedback",component:Feedback},
           {  path:"/equipment",component:Equipment},
           {  path:"/equipmentAdd",component:Equipment_add},
           {  
             path:"/getEqData",
             name:'getEqData',
             component:getEqData,
            },
        ]
    },
    
    
]

const router = new VueRouter({
  routes //routes : routes
})
//
router.beforeEach((to,from,next)=>{
  
      document.title = to.matched[0].meta.title;   
      
      //to
      //from
      //next next(url)urlnextto
      if(to.path=='/login') return next(); //url/loginlogin
      const userFlag = window.sessionStorage.getItem("user");//sessionuser
      if(!userFlag)   //login
      {
          return next('/login'); //login
      }
      else{
         next(); //;  
      }

});
export default router //
