<template>
 <!-- #272B34;#DEB185 -->
 
  <!-- <div>
        this 
        <el-button type="info" @click="logout"> </el-button>
  </div>-->
  <!-- containerdiv -->
  <!-- container -->
  <el-container class="el-container">
    <!--  -->
    <el-header>
      <div>
        <img src="../assets/logo.png" alt class="header-img" />
        <span style="color:#DEB185"></span>
        <span class="toggle-button" style="width:25px" @click="toggleCollapase"></span>
      </div>
      <img alt="" title="" class="toggle_user" @click="logout" src="../assets/logout.png">
      <!-- <el-button class="toggle_user" type="info" @click="logout"></el-button> -->
    </el-header>
    <el-container>
      <!--  -->
      <el-aside :width="isCollapse?'64px':'200px'">
        <!-- -->
        <!-- @open="handleOpen"
              @close="handleClose"
              unique-opened 
        -->
        <!-- <div class="toggle-button" @click="toggleCollapase">|||</div> -->
        <el-menu
          :default-active="activePath"
          class="el-menu-vertical-demo"
          background-color="#545c64"
          text-color="#DEB185"
          active-text-color="#ffd04b"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          :router="true"
        >
          <!--  -->
          <el-submenu :index="item.id+''" v-for="item in menuList" :key="item.id">
            <template slot="title">
              <i :class="iconObject[item.id]"></i>
              <span>{{item.title}}</span>
            </template>
            <!-- <el-menu-item-group>
                  <template slot="title"></template>
                  <el-menu-item index="1-1">1</el-menu-item>
                  <el-menu-item index="1-2">2</el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group title="2">
                  <el-menu-item index="1-3">3</el-menu-item>
            </el-menu-item-group>-->

            <!--  -->
            <el-menu-item :index="it.path" v-for="it in item.sList" :key="it.id" @click="saveNavState(it.path)">
              <template slot="title">
                <i :class="iconObject[it.id]"></i>
                <span>{{it.title}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
          <!-- <el-menu-item index="2">
                <i class="el-icon-menu"></i>
                <span slot="title"></span>
              </el-menu-item>
              <el-menu-item index="3">
                <i class="el-icon-setting"></i>
                <span slot="title"></span>
          </el-menu-item>-->
        </el-menu>
      </el-aside>
      <el-container>
        <!--  -->
        <el-main>
            <!-- MAIn -->
            <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      //
      menuList: [],
      isCollapse: false, //
      iconObject: {  //keyvalue
        '100': "el-icon-user-solid",
        '200': "el-icon-s-tools",
        '300': "el-icon-s-promotion",

        '101': "el-icon-s-order",
        '102': "iconfont icon-mima",
        '103': "iconfont icon-wode1",
        '104': "iconfont icon-mima",
        '201': "el-icon-plus",
        '202': "el-icon-cpu",
        '203': "iconfont icon-wode1",
        '204': "iconfont icon-mima",
       
        '301': "el-icon-s-comment",
      },
      activePath:"/welcome",

    };
  },
  //
  created() {
    //alert("")
    const activePath = window.sessionStorage.getItem("activePath");
    this.activePath = activePath;
    this.getMenuList();
  },
  methods: {
    logout() {
      window.sessionStorage.clear(); //session
      this.$router.push("/login"); //loginlogin
    },
    // 
    async getMenuList() {
      const { data: res } = await this.$http.get("menus");
      console.log("home.vue-->");
      console.log(res);
      if (res.flag != 200) return this.$message.error("!!");
      this.menuList = res.menus; //datamenuList
    },
    toggleCollapase() {
      this.isCollapse = !this.isCollapse;
    },
    saveNavState(activePath){
      window.sessionStorage.setItem("activePath",activePath);
      this.activePath = activePath;
    },
  },
};
</script>
<style lang="less" scoped>
body{
  background:#272B34;
 
}
.el-container {
  height: 100%;
  color: #000;
}
.el-header {
  background-color: #333744;
  display: flex;
  justify-content: space-between; //flex
  align-items: center;
  color: #fff;
  padding-left: 0px; //
  div {
    //div
    display: flex;
    align-items: center;
    font-size: 20px;
      margin-left: 15px;
  }
}
.el-aside {
  background-color: #373d41;
  .el-menu {
    border-right: none; //
  }
}
.el-main {
  background-color: #272B34;
}
.header-img {
  height: 30px;
  width: 30px;
  border-radius:50%;
  background: #fff;
  margin-right:5px
}
.el-col-12 {
  width: 100%;
}
.toggle-button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px; //
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em; //
  cursor: pointer; //
}
.toggle_user{
   border-radius:50%;
   border:none;
   height:20px;
}
</style>