<template>
<div class="container">
     <el-breadcrumb style="" class="home" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/feedback' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/feedback' }"></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadaw="hover" style="border:1px solid #000;box-shadow:10px 10px 5px #000;">
      <h3 style="height:20px;margin-top:0px"></h3>
      <div class="select">
            <el-select v-model="value[0]"  placeholder="">
                <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.label">
                </el-option>
            </el-select>
             <el-select v-model="value[1]" placeholder="">
                <el-option
                v-for="item in options2"
                :key="item.value"
                :label="item.label"
                :value="item.label">
                </el-option>
            </el-select>
             <el-select  v-model="value[2]" placeholder="">
                <el-option
                v-for="item in options3"
                :key="item.value"
                :label="item.label"
                :value="item.label">
                </el-option>
            </el-select>
            <el-button icon="el-icon-search" circle @click="searchResult"></el-button>
      </div>  
      <div class="labelStyle">
        <strong></strong>
        <strong  v-for="(item,index) in value" :key="index">
          <el-tag :type="tag_type[index]" style="margin-right:10px" :disable-transitions="true" closable @close="handleTag(index)" v-show="item">{{item}}</el-tag>
        </strong>
      </div>
      <el-table :data="data_FB" border style="box-shadow:10px 10px 5px #e3e3e3;" ref="filterTable" 
       :cell-style="bodyCell"
       :header-cell-style="headerCell"
       >
        <el-table-column prop="time"  width="140px" label=""></el-table-column>
        <el-table-column prop="username" width="100px" label=""></el-table-column>
        <el-table-column prop="fbtype"  width="140px" label=""></el-table-column>
        <el-table-column prop="content" label=""></el-table-column>
        <el-table-column prop="state"  width="100px" label="" :filters="[{text:'',value:''},{text:'',value:''}]" :filter-method="filter_state">
          <template slot-scope="scope">
            <span v-if="scope.row.state"></span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="admin"  width="100px" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
              <el-button @click="editFB(scope.row)"></el-button>
              <el-button @click="deleteFB(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

    </el-card>
      <el-dialog title="" :visible.sync="handleDialogVisible" width="30%" >
          <el-form :model="currentFB">
              <el-form-item label=":">
                  <el-input v-model="currentFB.content"></el-input>
              </el-form-item>
              <el-form-item label=":" >
                  <el-input v-model="currentFB.fbcontent"></el-input>
              </el-form-item>
              <el-form-item label=":">
                  <el-input v-model="currentFB.admin"></el-input>
              </el-form-item>
          </el-form>
          <span slot="footer">
              <el-button @click="handleDialogVisible=false"></el-button>
              <el-button @click="updateFB()"></el-button>
          </span>
    </el-dialog>
     <!--  -->
    <div class="block" >
      <span class="demonstration"></span>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[2,3,5]"
        :page-size="3"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
</div>
</template>
<script>
export default {
 data () {
   return {
   options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
   options2: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
   options3: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
   handleDialogVisible:false,
   //option
   value:["","",""],
   // 
   tag_type:["success","info","warning"],      
   data_FB:[{
     time:'2021',
     username:'xj',
     content:'sf',
     admin:'lcc',
     FBcontent:"....",
     state:''
   },{
   }
   
   ],
   currentFB:{}, 
   queryInfo: {
        query: "", //
        pageNum: 1, //
        pageSize: 3, //
      },
   total:10,   
   FBlist:[],
   }
 },
created(){
   this.getFBList();
 },
watch:{
   value(val){
    // //  console.log(val)
    //  this.data_FB = this.data_FB.filter(item=>{
    //    console.log(item)
    //    return item.state === true
    //  })
    //
    if(val.join("")==0)
    {
      this.queryInfo.query="";
      this.getFBList();
    }
    else{
      this.searchResult();
    }

   },
  data_FB(){
    // this.value=null;
  }
   
 },
  methods: {
    //
    headerCell(e){
       return "background:#272B34;color:#DEB185;text-align:center;font-size:15px"
    },
    //
    bodyCell(e){
      let res = "background:#409EFF;color:#fff;text-align:center;width:20px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap !important;";
      if(e.columnIndex == 6)
      {
      return res;
      }
      else{
        return res+"border-right:1px solid #fff;r";
      }
    },
    //
    async searchResult(){
      //console.log('')
      this.queryInfo.query = this.value;
      const {data:res} = await this.$http.post(`searchResult`,this.queryInfo)
    //  console.log(res)
       this.data_FB = res.FBAll;
       this.total = res.numbers;
    },
     //
     async getFBList(){
        const {data:res}  = await this.$http.get("getFBAll",{
          params: this.queryInfo,
        });
        // console.log(res)
        this.data_FB = res.FBAll;
        this.total = res.numbers;
     },
    //
    filter_state(e,row){
      // console.log(e,row);
      if(e==="")
      {return row.state==true;}
      else{
      return row.state==false;
      }
    },
    // ,
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      // this.getUserList();
    },
    //
    handleCurrentChange(newSize) {
      this.$refs.filterTable.clearFilter();
      this.queryInfo.pageNum = newSize;
      if(typeof this.queryInfo.query !="object")
      this.getFBList();
      else{
        this.searchResult()
      }
    },
    //
    editFB(e){
        this.currentFB=e;
        // console.log(e)
        this.handleDialogVisible=true;
     },
    //
    async updateFB(){
         const {data:res} = await this.$http.post("editFB",this.currentFB);
         if(res==1) {
           this.$message.success("!!!");
           this.searchResult();
           this.handleDialogVisible=false;
         }
        //  console.log(res)
     },
    //
    async deleteFB(row){
      // console.log(row)
      const {data:res} = await this.$http.post("deleteFB",row);
      // console.log(res);
      if(res==1)
      {
        this.$message.success("!!!");
        this.searchResult();
      }else{
        this.$message.error("!!!");
      }

    },
    //
    handleTag(e){
      this.value.splice(e,1,"");
    }
  }
}   
</script>
<style lang="less" scoped>
/deep/ .el-table .cell{
  white-space: nowrap !important;
}
.container{
   color:#DEB185 !important;
  .select{
    .el-select{
      margin-right:20px;
    }
  }
  .labelStyle{
    display: flex;
    // justify-content: center;
    align-items: center;
    margin-top:20px;
    margin-bottom:20px;
    font-size: 20px;
  }
 }
.home /deep/ .el-breadcrumb__inner.is-link{
    color:#DEB185;
  }
  .home /deep/ .el-breadcrumb__inner.is-link:hover{
    color: blue;
  }
  .el-breadcrumb {
    //
    margin-bottom: 15px;
    font-size: 15px;
  }
</style>